<template>
  <div class="scout-menu">
    <div class="w-main scout-menu-main">
      <img class="scout-menu-logo MR-20 pointer" src="@/assets/images/logo.png" alt />
      <div class="scout-menu-list">
        <div class="scout-menu-list-item" :class="{ 'active': $route.path == '/swap' }">
          <span class="scout-menu-text">
            <router-link to="/swap">Swap</router-link>
          </span>
        </div>
        <div class="scout-menu-list-item" :class="{ 'active': $route.path == '/pledge' }">
          <span class="scout-menu-text">
            <router-link to="/pledge">Stake</router-link>
          </span>
        </div>
      </div>
      <div class="menu-right">
        <img @click="drawer = true" class="scout-menu-menu pointer" src="./menu.png" alt />
        <el-dropdown v-if="userName">
          <div class="login pointer">{{ userName }}</div>
          <template #dropdown>
            <el-dropdown-menu>
              <el-dropdown-item @click="loginout">logout</el-dropdown-item>
            </el-dropdown-menu>
          </template>
        </el-dropdown>
        <div v-else @click="loginFn" class="login pointer">Connect</div>
      </div>
    </div>
  </div>
  <el-drawer v-model="drawer" title size="50%" direction="rtl">
    <el-menu>
      <el-menu-item @click="goPath('/swap')">
        Swap
      </el-menu-item>
      <el-menu-item @click="goPath('/pledge')">
        Stake
      </el-menu-item>
    </el-menu>
  </el-drawer>

  <el-dialog
    v-model="dialogVisible"
    title="连接钱包"
    width="420px"
  >
    <el-row>
      <el-col :span="12" @click="connectWallet(1)" class="flex-column-center align-center pointer">
        <img src="@/assets/images/metamask.png" class="connect-item__icon MB-10"/>
        Metamask
      </el-col>
      <el-col :span="12" @click="connectWallet(2)" class="flex-column-center align-center pointer">
        <img src="@/assets/images/walletconnect.png" class="connect-item__icon MB-10"/>
        WallatConnect
      </el-col>
    </el-row>
    <el-divider />
    <el-button status="danger" @click="connectWallet(3)">
      <template #icon>
        <icon-exclamation-circle-fill />
      </template>
      私钥连接
    </el-button>
  </el-dialog>
</template>

<script setup>
/* global wallet:false */ 
import { ref } from 'vue'
import { useRouter } from 'vue-router'
const router = useRouter()
const drawer = ref(false)
const dialogVisible = ref(false)
const userName = ref('')
const loginFn = async () => {
  dialogVisible.value = true
  // await wallet.connect(1)
}
const connectWallet = (type) => {
  let key = ''
  if (type == 3) {
    key = prompt('输入私钥')
    if (!key) {
      return
    }
  }
  wallet.connect(type, key)
  dialogVisible.value = false
}
const loginout = async () => {
  wallet.logout()
}

const goPath = (path) => {
	drawer.value = false;
	router.push(path)
}
if (wallet.accounts[0]) {
  userName.value = '0x...' + wallet.accounts[0].slice(-4)
}

// 监听用户信息切换
wallet.on('accountChange', () => {
  try {
    userName.value = '0x...' + wallet.accounts[0].slice(-4)
  } catch (error) {
    userName.value = ''
  }
})
</script>

<style lang="scss" scoped>
.connect-item__icon{
  height: 40px;
  width: auto;
}
.scout-menu {
  background-color: rgba(27, 27, 27, 1);
  height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.scout-menu-main {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.scout-menu-logo {
  width: 0.82rem;
  height: 0.82rem;
}
.scout-menu-menu {
  width: 0.28rem;
  height: 0.2rem;
  display: none;
}
.scout-menu-list {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex: 1;
  margin-right: 0.4rem;
}
.scout-menu-list-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0rem 0.3rem;
  height: 50px;
  transition: all 0.3s;
  &.active {
    background: rgba(252, 191, 47, 1);
  }
}
.scout-menu-icon {
  width: 0.2rem;
  height: 0.2rem;
  margin-right: 0.2rem;
}
.scout-menu-text {
  color: rgba(237, 237, 237, 1);
  font-size: 16px;
}
.el-drawer__close-btn {
  padding: 0rem;
}
.menu-right {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.menu-right .login {
  margin-left: 0.3rem;
  padding: 0.1rem 0.3rem;
  background-color: #FCBE2F;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.16rem;
  color: #fff;
  border-radius: 0.06rem;
}
@media only screen and (max-width: 992px) {
  .scout-menu-list {
    display: none;
  }
  .scout-menu-menu{
		display: block !important;
	}
}
@media only screen and (max-width: 768px) {
	
}
</style>
<style>
.el-menu {
  background-color: transparent !important;
  border: 0 !important;
}
.el-menu-item {
  color: #fff !important;
}
.el-menu-item:hover {
  color: #fff !important;
  background-color: rgb(79, 75, 75) !important;
}
.router-link-active {
  color: #fff;
}
a:-webkit-any-link {
  color: #fff;
}
a:-webkit-any-link:active {
  color: #fff;
}
.el-drawer__header span {
  color: #fff;
}
</style>